{
    id: 2,
        date
:
    "26 maja 2023 roku",
        title
:
    "Bieganie, czyli powrt na swj tor",
        body
:
    (
        <>
            <p>
                Nasta czas refleksji dotyczcej mojej <s>ostatniej</s> oglnej aktywnoci fizycznej.
                Jeszcze
                rok
                temu, dokadnie 14 maja 2022, speniam swoje marzenie o wziciu udziau w biegu grskim
                na
                <i>SCARPA
                    Ultra Trail</i>. Zadanie byo to nieatwe, bo musielimy pokona stromy odcinek 600
                m rnicy
                midzy
                poziomami. Dla amatora, takiego jak ja, pokonanie 10 km biegnc po wzniesieniu byo mega
                wyzwaniem.
                Razem z P. bylimy
                ostatni. Ale satysfakcja bya niesamowita!
            </p>
            <Q>No tak, ale przecie bylicie ostatni.</Q>
            <p>
                Czy bylimy pierwsi czy ostatni  nie miao to dla mnie wikszego znaczenia. No, dobra,
                gdybym
                ya
                z nagrd w zawodach i ze sponsoringu partnerw - <em>wtedy</em> miaoby to dla mnie
                ogromne
                znaczenie! Ale
                dopki jestem amatorem, ciesz si tak drobnostk, bo pamitam jak to byo na pocztku.
            </p>
            <p>
                Pocztek to moment budowania bazy  pamitam jak nie potrafiam przebiec nawet penej
                minuty.
                Jednak
                z kadym treningiem, kadym kilometrem i kad <em>waciwie</em> spdzon regeneracj
                stawaam
                si
                silniejsza, a moja wytrzymao rosa.</p>
            <Figure>
                <img alt="Katarzyna na tle wzniesienia Mogielicy" className="article__image"
                     src={Kate}/>
                <Figcaption><i>Scarpa Ultra Trail</i> 2022 Mogielica
                </Figcaption>
            </Figure>
            <p>
                Dlaczego takiej samej metodyki nie zastosowaam przy moich pocztkach nauki
                programowania?
                Dlaczego
                dopiero teraz dotaro do mnie, e mzg rwnie potrzebuje regeneracji i jak w przypadku
                intensywnych
                treningw minie doprowadzi si do zakwaszenia, tak i tutaj dojdzie do <i>przeadowania
                informacji</i>?
            </p>
            <p>
                Bd nowicjusza  tak bym to skomentowaa. Przychylniej patrz teraz na swoje bdy i na
                ich
                podstawie wycigam wnioski.
            </p>
            <p>
                Wracam do biegania i do przerabiania materiau z kursu jeszcze raz. Tym razem zastosuj
                przerwy
                (regeneracj) i krtsze dystanse (okresy na nauk) na pocztek.
                <Q>Blog o programowaniu? A gdzie tu <em>JavaScript</em>?</Q>
                Ju spiesz z odpowiedzi - <strong>prowadz bloga na moim totalnie pierwszym projekcie
                na
                pocztku
                mojej
                cieki. Z kad powtrzon wiedz zamierzam udoskonala bloga, tak aby by ywym
                tworem,
                pokazujcym moj nauk rwnie poprzez praktyk. A e nie samym programowaniem czowiek
                yje
                
                na
                pewno bd tutaj przemycaa historie z mojego dowiadczenia z bieganiem</strong>.
            </p>
            <p>
                Trzymacie si ciepo i nie wtpcie w siebie!
            </p>
        </>
    ),
}
